.nav {
  &__list {
    display: flex;
    margin: 0;
    list-style: none;
  }

  &__item {
    margin-left: 20px;
  }

  &__link {
    display: flex;
    text-decoration: none;
    line-height: 60px;
    text-transform: uppercase;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    color: #060b35;
  }

  &__link:hover {
    color: $button-color;
  }

  .is-active {
    color: $button-color;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 4px;
      bottom: 0;
      background: $button-color;
      border-radius: 8px;
    }
  }
}
